.card-item {
  padding: 15px 15px 20px 15px;
  position: relative;
  border-radius: 5px;
  min-height: 185px;
  box-sizing: border-box; }
  @media (max-width: 767px) {
    .card-item {
      margin-bottom: 12px; } }
  .card-item-footer {
    display: block;
    position: absolute;
    bottom: -1px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    left: 0;
    right: 0;
    text-align: center;
    font-size: 10px;
    line-height: 14px;
    font-family: "Roboto Regular";
    color: #ffffff; }
    .card-item-footer-green {
      background-color: #7fb345; }
    .card-item-footer-orange {
      background-color: #ff7200; }
    .card-item-footer-red {
      background-color: #e00b3d; }
    .card-item-footer-blue {
      background-color: #29d1d4; }
  .card-item-bordered-orange {
    border: 1px solid #ff7200; }
  .card-item-bordered-green {
    border: 1px solid #7fb345; }
  .card-item-bordered-gray {
    border: 1px solid #dadada; }
  .card-item .icon-action {
    position: absolute; }
  .card-item .button-action {
    position: absolute;
    bottom: 20px;
    right: 15px; }
  .card-item .button {
    position: absolute;
    bottom: 20px; }
  .card-item .info {
    position: absolute; }
