  justify-content: flex-start; }

.content-slider {
  position: relative; }
  .content-slider-item {
    position: relative; }
  .content-slider-prev, .content-slider-next {
    position: absolute;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 12%;
    color: #fff;
    text-align: center; }
    .content-slider-prev-icon, .content-slider-next-icon {
      display: inline-block;
      width: 27px;
      height: 43px;
      background: transparent no-repeat center center;
      background-size: 100% 100%;
      cursor: pointer; }
      @media (max-width: 576px) {
        .content-slider-prev-icon, .content-slider-next-icon {
          width: 18px;
          height: 29px;
          background-size: cover !important; } }
  .content-slider-prev {
    left: 0; }
    .content-slider-prev-icon {
      background: url(data:image/png;base64,); }
  .content-slider-next {
    right: 0; }
    .content-slider-next-icon {
      background: url(data:image/png;base64,); }
  .content-slider-indicators {
    position: absolute;
    bottom: 20px;
    z-index: 99;
    text-align: center;
    left: 0;
    right: 0; }
    @media (max-width: 576px) {
      .content-slider-indicators {
        bottom: 7px; } }
    .content-slider-indicators span {
      width: 12px;
      height: 12px;
      background-color: #606060;
      display: inline-block;
      border-radius: 50%;
      cursor: pointer;
      margin-right: 15px;
      border: 1px solid #ccc; }
      .content-slider-indicators span.active {
        background-color: #ffffff; }
  .content-slider .content-icon-add-green {
    position: absolute;
    right: 100px;
    bottom: -22px; }
    @media (max-width: 576px) {
      .content-slider .content-icon-add-green {
        width: 40px;
        height: 40px;
        background-size: 20px;
        bottom: -15px;
        right: 20px; } }
