.custom-title {
  font-size: 20px;
  margin: 0;
  margin-bottom: 5px;
  color: #29d1d4;
  font-family: "Roboto Light";
  position: relative; }
  .custom-title-blue {
    color: #0072aa; }
  .custom-title-heading {
    padding-left: 60px; }
    .custom-title-heading .custom-title-icon {
      position: absolute;
      left: -60px; }
  .custom-title-icon {
    width: 51px;
    height: 51px;
    display: inline-block;
    vertical-align: top;
    margin-right: 7px; }
    .custom-title-icon-puzzle {
      background: url(data:image/png;base64,) no-repeat 50%; }
    .custom-title-icon-object {
      background: url(data:image/png;base64,) no-repeat 50%; }
  .custom-title.image {
    padding-left: 65px; }
    .custom-title.image .custom-title-icon {
      left: 0; }
