/* Colors */
/* Fonts */
.file-upload-title {
  color: #ffffff;
  font-size: 13px;
  line-height: 17px;
  font-family: "Roboto Light";
  padding-left: 35px;
  position: relative;
  cursor: pointer;
  display: inline-block; }

.file-upload-icon {
  background: url(data:image/png;base64,) no-repeat 50%;
  width: 27px;
  height: 22px;
  display: inline-block;
  position: absolute;
  left: 0; }

.file-upload-item {
  margin-bottom: 15px;
  position: relative; }
  .file-upload-item-icon {
    display: inline-block;
    margin-right: 10px;
    width: 21px;
    height: 21px;
    background-size: contain;
    vertical-align: middle; }
    .file-upload-item-icon.file {
      background: url(data:image/png;base64,) no-repeat; }
      .file-upload-item-icon.file.success {
        background-position: 0 0; }
      .file-upload-item-icon.file.warning {
        background-position: 0 -60px; }
      .file-upload-item-icon.file.error {
        background-position: 0px -129px; }
    .file-upload-item-icon.zip {
      background: url(data:image/png;base64,) no-repeat; }
      .file-upload-item-icon.zip.success {
        background-position: 0 0; }
      .file-upload-item-icon.zip.warning {
        background-position: 0 -60px; }
      .file-upload-item-icon.zip.error {
        background-position: 0px -129px; }
  .file-upload-item-title {
    font-size: 12px;
    line-height: 15px;
    font-family: "Roboto Regular"; }
    .file-upload-item-title.success {
      color: #7fb345; }
    .file-upload-item-title.warning {
      color: #a7a9ac; }
    .file-upload-item-title.error {
      color: #e00b3d; }
  .file-upload-item-progress {
    height: 3px;
    display: block;
    width: 100%;
    margin-top: 3px; }
    .file-upload-item-progress.success {
      background-color: #7fb345; }
    .file-upload-item-progress.warning {
      background-color: #ff7200; }
    .file-upload-item-progress.error {
      background-color: #e00b3d; }
  .file-upload-item-info {
    font-size: 12px;
    line-height: 15px;
    font-family: "Roboto Regular";
    color: #7fb345;
    float: right;
    padding-right: 20px;
    padding-top: 2px; }
    .file-upload-item-info.success {
      color: #7fb345; }
    .file-upload-item-info.warning {
      color: #a7a9ac; }
    .file-upload-item-info.error {
      color: #e00b3d; }
  .file-upload-item .icon-close {
    top: 3px;
    right: 0;
    position: absolute; }
