.center-both {
  display: flex;
  align-items: center;
  justify-content: center; }

.center-vertical {
  display: flex;
  align-items: center; }

.center-horizontal {
  display: flex;
  justify-content: center; }

.center-none {
  display: flex;
  align-items: stretch;
  justify-content: flex-start; }

.button {
  position: relative;
  border: none;
  outline: none;
  font-size: 12px;
  color: #ffffff;
  text-align: center;
  font-family: "Roboto Regular";
  padding: 8px 30px;
  cursor: pointer; }
  .button.linear {
    border-radius: 15px; }
  .button.squared {
    border-radius: 3px;
    padding: 6px; }
  .button-regular-orange {
    background-color: #ff7200; }
  .button-regular-green {
    background-color: #7fb345; }
  .button-regular-blue {
    background-color: #29d1d4; }
  .button-regular-red {
    background-color: #e00b3d; }
  .button-regular-gray {
    background-color: #a7a9ac; }
  .button-bordered {
    background-color: transparent;
    padding: 7px 28px; }
    @media (max-width: 576px) {
      .button-bordered {
        padding: 6px 22px; } }
    .button-bordered-black {
      color: #373737;
      border: 1px solid #3e3e3e;
      background-color: transparent; }
    .button-bordered-orange {
      color: #ff7200;
      border: 1px solid #ff7200;
      background-color: transparent; }
    .button-bordered-green {
      color: #7fb345;
      border: 1px solid #7fb345;
      background-color: transparent; }
      .button-bordered-green:hover {
        color: #ffffff;
        background-color: #88b917; }
    .button-bordered-blue {
      color: #29d1d4;
      border: 1px solid #29d1d4;
      color: #29d1d4;
      background-color: transparent; }
    .button-bordered-red {
      color: #e00b3d;
      border: 1px solid #e00b3d;
      background-color: transparent; }
    .button-bordered-gray {
      color: #a7a9ac;
      border: 1px solid #a7a9ac;
      background-color: transparent; }
    .button-bordered-white {
      border: 1px solid #ffffff;
      background-color: transparent; }
  .button .icon-action {
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    position: absolute; }
