.header {
  background-color: #232f39;
  box-sizing: border-box; }
  .header .wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center; }
  .header-inner {
    display: flex;
    flex-direction: row; }
  .header .wrap.wrap-right {
    justify-content: flex-end;
    flex: 1 0 76%; }
  .header .wrap-right .submenu-header:last-child .icons-wrap {
    margin: 0; }
  .header .icon-user:before {
    font-size: 32px; }
  .header .submenu-header button.squared {
    margin: 10px 12px;
    font-size: .875rem; }
