.switcher {
  /* Rounded sliders */ }
  .switcher .switch {
    position: relative;
    display: inline-block;
    width: 67px;
    height: 21px; }
    .switcher .switch input {
      display: none; }
    .switcher .switch-slider {
      width: 67px;
      height: 21px;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #f4f4f4;
      border: 1px solid #dcdcdc;
      transition: .4s;
      cursor: pointer; }
    .switcher .switch-slider:before {
      content: "";
      position: absolute;
      height: 19px;
      width: 19px;
      text-align: center;
      color: #373737;
      font-size: 12px;
      line-height: 14px;
      font-family: "Roboto Regular";
      line-height: 19px;
      background-color: #ffffff;
      border: 1px solid #dcdcdc;
      transition: .75s; }
  .switcher input + .switch-slider:after {
    content: "hide";
    height: 21px;
    display: block;
    box-sizing: border-box;
    width: 100%;
    padding-right: 15px;
    color: #373737;
    font-size: 12px;
    line-height: 20px;
    font-family: "Roboto Regular";
    padding-left: 26px;
    line-height: 18px;
    transition: .75s; }
  .switcher input:checked + .switch-slider:after {
    content: "show";
    padding-left: 15px;
    text-align: left;
    background: #7fb345;
    width: 67px;
    height: 21px;
    border-radius: 21px;
    color: #ffffff; }
  .switcher input:checked + .switch-slider:before {
    -webkit-transform: translateX(46px);
    transform: translateX(46px); }
  .switcher .switch-slider.switch-round {
    border-radius: 11px; }
  .switcher .switch-slider.switch-round:before {
    width: 19px;
    border-radius: 19px; }
  .switcher-title, .switcher-status {
    display: block;
    color: #4e4d4d;
    font-size: 12px;
    line-height: 14px;
    font-family: "Roboto Regular"; }
  .switcher-title {
    margin-bottom: 10px;
    min-height: 14px; }
  .switcher-status {
    margin-bottom: 5px; }
