/* Colors */
/* Fonts */
.submenu-top, .submenu-bottom {
  display: flex; }

.submenu-top {
  position: relative;
  padding: 6px 20px;
  flex-wrap: wrap;
  align-items: center;
  background-color: #232f39; }
  .submenu-top .icons-toggle-arrow {
    position: absolute; }

.submenu-toggle {
  display: none; }

.submenu-items {
  list-style: none;
  padding: 0;
  margin: 0; }

.submenu-item:not(:first-of-type) .submenu-item-title {
  padding-left: 20px; }

.submenu-item-link {
  display: block;
  padding: 10px;
  font-family: "Roboto Light";
  color: #ffffff;
  font-size: .8rem;
  padding-left: 15px; }

.submenu-item-count {
  float: right; }

.submenu-item-title {
  position: relative; }

.submenu-item-dot {
  position: absolute;
  left: 0;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }
  .submenu-item-dot.dot-red {
    background-color: #ed1b23; }
  .submenu-item-dot.dot-orange {
    background-color: #ffa125; }
  .submenu-item-dot.dot-gray {
    background-color: #818185; }
  .submenu-item-dot.dot-gray-light {
    background-color: #cdcdcd; }
  .submenu-item-dot.dot-green {
    background-color: #87bd23; }
  .submenu-item-dot.dot-blue {
    background-color: #2ad1d4; }

.submenu-toggle {
  position: absolute;
  left: 0px;
  background-color: #232f39;
  padding: 10px;
  top: 100%;
  z-index: 9;
  box-sizing: border-box;
  text-align: left;
  width: 100%; }

.submenu-active {
  background-color: #000915; }
  .submenu-active .submenu-toggle {
    display: block; }

.submenu-header .profile-user-type, .submenu-header .profile-user-edit, .submenu-header .profile-user-name {
  font-family: "Roboto Light";
  color: #ffffff;
  text-decoration: none;
  font-size: .8rem; }

.submenu-header .profile-user-type {
  display: inline-block;
  width: 51%;
  overflow: hidden;
  text-overflow: ellipsis; }

.submenu-header .profile-user-edit {
  margin-left: 4px;
  float: right;
  text-decoration: underline; }

.submenu-header .profile-user-name {
  display: block;
  margin-bottom: 10px; }

.submenu-header .profile-user-button {
  font-size: .785rem;
  color: #ffa225;
  text-decoration: none;
  font-family: "Roboto Light";
  text-align: left;
  border-radius: 16px;
  border: 1px solid #ffa125;
  background-color: transparent;
  display: block;
  width: 100%;
  outline: none;
  position: relative;
  padding: 5px 27px 5px 10px;
  box-sizing: border-box; }
  .submenu-header .profile-user-button:hover {
    color: #000916;
    background-color: #ffa125; }
    .submenu-header .profile-user-button:hover .btn-logout-icon:before, .submenu-header .profile-user-button:hover .icon-copied:before {
      color: #000915; }
  .submenu-header .profile-user-button span:first-child {
    line-height: 17px;
    display: block; }

.submenu-header .profile-bookmark-link {
  margin: 14px 9px 13px;
  width: 62%;
  border: 1px solid #bcbdc0;
  padding: 7px 6px;
  border-radius: 3px;
  outline: none;
  box-sizing: border-box; }

.submenu-header .btn-logout-icon {
  width: 19px;
  height: 19px;
  position: absolute;
  right: 9px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%); }
  .submenu-header .btn-logout-icon:before {
    content: "\E90D";
    color: #ffa125;
    font-size: 19px; }
  .submenu-header .btn-logout-icon.icon-copied:before {
    content: "\E90C";
    color: #000915;
    color: #ffa125; }

.submenu-header .button-wrap .btn {
  margin-top: 10px;
  float: right; }
